@import '../../global.scss';

.navbar{
  width: 100%;
  height: 70px;
  background-color: white;
  color: $mainColor;
  position: fixed;
  top: 0;
  z-index: 2;

  .wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left{
      display: flex;
      align-items: center;

      .logo{
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        margin-right: 40px;
      }
    }

    .right{
      .hamburger{
        text-decoration: none;
        margin: 5px;
        font-size: 20px;
        // font-weight: 300;
        cursor: pointer;

        &:hover{
          font-weight: bold;
        }
      }
    }
  }
}