@import '../../global.scss';

.portfolio{
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1{
    font-size: 50px;
  }

  ul{
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .container{
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 10px;
    margin: 10px;

    .item{
      width: 250px;
      height: 160px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all .5s ease;
      cursor: pointer;

      h3{
        position: absolute;
        font-size: 20px;
      }

      a{
        img{
          // margin: 20px;
          width: 100%;
          height: 100%;
          object-fit: cover;
          z-index: 1;
        }
      }

      &:hover{
        background-color: $mainColor;
        img{
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
